import React, { useState, useEffect } from 'react';
import './App.css';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Grid from '@material-ui/core/Grid';
import ContentBox from './Components/ContentBox';
import MainAppBar from "./Components/MainAppBar";
import InfoBox from "./Components/InfoBox";
import TableView from './Components/TableView';
import {MainLoader, BoxLoader} from './Components/ContentLoaders'

const useStyle = makeStyles(theme => ({
  root: {
      padding: theme.spacing(2),
  },
  selectStyle:{
    fontSize: 24
  }
}))

function App() {
  const classes = useStyle();
  const [countries, setCountries] = useState([]);
  const [country, setCountry] = useState("worldwide");
  const [corona, setCorona] = useState({});
  const [tableData, setTableData] = useState([]);
  const [isLoaded, setIsLoaded] = useState(false);


  useEffect(() => {
    setIsLoaded(false);
    fetch('https://disease.sh/v3/covid-19/all')
      .then((response) => response.json())
      .then((data) => {
        
        setCorona({
          totalCases: data.cases,
          active: data.active,
          deaths: data.deaths,
          recovered: data.recovered,
          todayCases: data.todayCases,
          todayRecovered: data.todayRecovered,
          todayDeaths: data.todayDeaths,
          lastUpdate: data.updated
        });
        setIsLoaded(true);
      })
  }, []);

  useEffect(() => {    
    const getCountryData = async () => {
      setIsLoaded(false);
      await fetch("https://disease.sh/v3/covid-19/countries")
      .then((response) => response.json())
      .then((data) => {
        const country = data.map((country) => ({
          name: country.country,
          value: country.countryInfo.iso2
        }));
        setCountries(country);
        setTableData(data);
        setIsLoaded(true);
      })
    };
    getCountryData();
  }, []);

  const handleCountryChange = async (event) => {
    setIsLoaded(false);
    const countryCode = event.target.value;
    const url = countryCode === "worldwide" 
      ? `https://disease.sh/v3/covid-19/all`
      : `https://disease.sh/v3/covid-19/countries/${countryCode}`;

    setCountry(countryCode);
    await fetch(url)
      .then((response) => response.json())
      .then((data) => {
        setCorona({
          totalCases: data.cases,
          active: data.active,
          deaths: data.deaths,
          recovered: data.recovered,
          todayCases: data.todayCases,
          todayRecovered: data.todayRecovered,
          todayDeaths: data.todayDeaths,
          lastUpdate: data.updated
        });
        setIsLoaded(true);
      })
      
  }

  const MainCases = () =>  {
    
     return (isLoaded ? <ContentBox country={country} cases={corona.totalCases} newCases={corona.todayCases} /> : <MainLoader />);
    };

  const BoxDetails = () => (isLoaded ?
                    <Grid container spacing={2}>
                        <InfoBox style={{background: "#ffa5b1"}} title="Deaths" total={corona.deaths} newCases={corona.todayDeaths} />
                        <InfoBox style={{background: "#9ee8d7"}} title="Recovered" total={corona.recovered} newCases={corona.todayRecovered} />
                        <InfoBox style={{background: "#c3d6ff"}} title="Active Cases" total={corona.active} newCases={corona.todayCases} />
                    </Grid> : <BoxLoader />)
  
  
  return (
    <div className="App">

      
        <MainAppBar />

        <Container maxWidth="md">


        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
          <FormControl variant="filled" fullWidth style={{marginBottom: 12}}>
              <Select 
                className={classes.selectStyle} 
                variant="outlined"
                onChange={handleCountryChange}
                value={country}>
                <MenuItem value="worldwide" name='Worldwide'>Worldwide</MenuItem>
                {
                  countries.map(country => (
                    <MenuItem value={country.value} name={country.name}>{country.name}</MenuItem>
                  ))
                } 
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={12} sm={6}>
              <MainCases />
          </Grid>
        </Grid>

        
        <BoxDetails />
         


          
          <TableView data={tableData} />

        </Container>
    </div>
  );
}

export default App;
