import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

const useStyle = makeStyles((theme) => ({
    root: {
        marginTop: theme.spacing(2),
    }
}));

function TableView({data}) {

    const sortedData = data.sort((a,b) => a.cases > b.cases ? -1 : 1);

   //console.log(sorted);

    const classes = useStyle();

    return (
        <div style={{marginTop: 30}}>
            <Typography variant="h4">Cases overview</Typography>
            <TableContainer component={Paper} className={classes.root}>
            <Table aria-label="caption table">
                <caption>Covid-19 data sourced from Worldometers</caption>
                <TableHead>
                <TableRow>
                    <TableCell></TableCell>
                    <TableCell>Countries</TableCell>
                    <TableCell align="right">Total Cases</TableCell>
                    <TableCell align="right">Deaths</TableCell>
                    <TableCell align="right">Recovered</TableCell>
                    <TableCell align="right">Tests</TableCell>
                </TableRow>
                </TableHead>
                <TableBody>
                {sortedData.map((country) => (
                    <TableRow key={country.country}>
                        <TableCell>
                            <img src={country.countryInfo.flag} width={24} />
                        </TableCell>
                        <TableCell component="th" scope="row">
                            {country.country}
                        </TableCell>
                        <TableCell align="right">
                            <Typography variant="body-1" component="p">{country.cases}</Typography> 
                            <Typography variant="caption" component="p" color="textSecondary">+{country.todayCases}</Typography>    
                        </TableCell>
                        <TableCell align="right">
                            <Typography variant="body-1" component="p">{country.deaths}</Typography> 
                            <Typography variant="caption" component="p" color="textSecondary">+{country.todayDeaths}</Typography>  
                        </TableCell>
                        <TableCell align="right">
                            <Typography variant="body-1" component="p">{country.recovered}</Typography> 
                            <Typography variant="caption" component="p" color="textSecondary">+{country.todayRecovered}</Typography>
                        </TableCell>
                        <TableCell align="right">{country.tests}</TableCell>
                    </TableRow>
                ))}
                </TableBody>
            </Table>
            </TableContainer>
        </div>
    )
}

export default TableView
