import React from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import CountUp from 'react-countup';

const useStyle = makeStyles(theme => ({
    root: {
        padding: theme.spacing(2),
        
    }
}))

function ContentBox({country, cases, newCases}) {
    const classes = useStyle();
    return (
            <Box>
                <Typography variant="caption">Total Cases ({country})</Typography>
                <Typography variant="h3" style={{color: "#d63d59"}}>
                   
                    <CountUp 
                        start={0}
                        end={cases}
                        duration={2}
                        separator=","
                        />
                  
                    
                </Typography>
                <Typography variant="subtitle1" gutterBottom style={{color: "#d63d59"}}>+
            
                    
                    <CountUp 
                        start={0}
                        end={newCases}
                        duration={2}
                        separator=","
                        />
                  
                
                </Typography>
            </Box>
    )
}

export default ContentBox;